@import url(https://fonts.googleapis.com/css?family=Roboto);
html {
  font-family: 'Roboto', sans-serif;
  font-size: 20px;
  box-sizing: border-box;
  text-align: center; }

body {
  color: white;
  background-color: #c1c8e4;
  background-image: url("https://www.transparenttextures.com/patterns/egg-shell.png"); }

body, h1, h2, h3, h4, p, ul {
  margin: 0;
  padding: 0; }

input[type="number"] {
  -webkit-appearance: textfield;
  -moz-appearance: textfield;
  appearance: textfield; }

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  -moz-appearance: none; }

.calculator, .calculator .meal-display, .calculator .meal-display .meal-totals .totals-table, .calculator .foods, .calculator .foods .food, .calculator .foods .food .details-display .details-label {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center; }

.calculator .meal-display {
  margin: 2em 0; }
  .calculator .meal-display h1.title {
    font-size: 2.5em;
    margin-bottom: 1em;
    color: #5680e9; }
  .calculator .meal-display .meal-totals {
    border: 0.5em solid #5680e9;
    border-radius: 10px;
    background-color: #8860d0; }
    .calculator .meal-display .meal-totals h2.totals-label {
      padding-bottom: .2em;
      background-color: #5680e9; }
    .calculator .meal-display .meal-totals .totals-table {
      width: 22em; }
      .calculator .meal-display .meal-totals .totals-table .meal-nutrient-total {
        display: -webkit-flex;
        display: flex;
        border: 0.05em solid #5680e9;
        width: 100%; }
        .calculator .meal-display .meal-totals .totals-table .meal-nutrient-total h3.total-column {
          display: -webkit-flex;
          display: flex;
          padding: .5em 0;
          width: 50%; }
          .calculator .meal-display .meal-totals .totals-table .meal-nutrient-total h3.total-column.label {
            -webkit-justify-content: flex-end;
                    justify-content: flex-end; }
          .calculator .meal-display .meal-totals .totals-table .meal-nutrient-total h3.total-column.amount {
            -webkit-justify-content: flex-start;
                    justify-content: flex-start; }
  .calculator .meal-display p.instructions {
    margin-top: 2em;
    padding: 0 .5em;
    color: #8860d0; }
  @media (min-width: 320px) and (max-width: 480px) {
    .calculator .meal-display .meal-totals .totals-table {
      width: 18em; } }

.calculator .foods {
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  max-width: 60em; }
  .calculator .foods .food {
    margin: 0 2em 1em;
    width: 16em;
    border: 0.3em solid #8860d0;
    border-radius: 10px;
    box-sizing: border-box;
    color: white;
    background-color: #5ab9ea; }
    .calculator .foods .food h3.food-label {
      margin: .5em 0 1em; }
    .calculator .foods .food .input-display .input {
      border: 0.2em solid #8860d0;
      border-radius: 10px;
      padding: .5em;
      background: white;
      margin: 0 0 10px 0;
      width: 4em; }
    .calculator .foods .food .details-display {
      width: 100%; }
      .calculator .foods .food .details-display .details-label {
        -webkit-flex-direction: row;
                flex-direction: row;
        -webkit-justify-content: space-between;
                justify-content: space-between;
        width: 100%;
        color: white;
        background-color: #8860d0;
        /*box-shadow: -1px -1px 3px 0px #1a1a1a4f;*/
        cursor: pointer; }
        .calculator .foods .food .details-display .details-label i.icon {
          font-size: 1.5em; }
          .calculator .foods .food .details-display .details-label i.icon.down-icon {
            padding: 0 .5em .3em; }
          .calculator .foods .food .details-display .details-label i.icon.up-icon {
            padding: .3em .5em 0; }
      .calculator .foods .food .details-display ul.nutrient-list .food-nutrient-total {
        display: -webkit-flex;
        display: flex;
        padding: .2em 0;
        border-bottom: 0.05em solid #8860d0; }
        .calculator .foods .food .details-display ul.nutrient-list .food-nutrient-total p.food-column {
          display: -webkit-flex;
          display: flex;
          width: 50%; }
          .calculator .foods .food .details-display ul.nutrient-list .food-nutrient-total p.food-column.label {
            -webkit-justify-content: flex-end;
                    justify-content: flex-end; }
          .calculator .foods .food .details-display ul.nutrient-list .food-nutrient-total p.food-column.amount {
            -webkit-justify-content: flex-start;
                    justify-content: flex-start; }

/*Media queries*/
@media (min-width: 320px) and (max-width: 480px) {
  body {
    font-size: .8em; } }

